<template>
  <div class="login">
    <div class="login-content">
      <el-form
        label-width="80px"
        label-position="right"
        :model="ruleForm"
        ref="ruleForm"
        class="demo-ruleForm"
      >
        <el-form-item v-if="type === ''" label="" prop="name">
          <el-input
            v-model="ruleForm.name"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input
            v-model="ruleForm.phone"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            type="password"
            v-model="ruleForm.password"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item prop="identity">
          <el-radio-group v-model="ruleForm.roleType">
            <el-radio label="2"></el-radio>
            <el-radio label="1"></el-radio>
            <el-radio label="3"></el-radio>
          </el-radio-group>
        </el-form-item>
        <!--  -->
        <el-form-item class="btn">
          <el-button
            style="width: 100%; margin: 0 auto"
            type="primary"
            @click="submitForm('ruleForm')"
            >{{ type === "" ? "" : "" }}</el-button
          >
          <div class="loginType" @click="loginType()">{{ type }}</div>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { login, register, getUserInfo } from "@/service/api/index";
export default {
  name: "Home",
  data() {
    return {
      // 
      type: "",
      ruleForm: {
        phone: "",
        name: "",
        password: "",
        roleType: "2",
      },
    };
  },
  components: {},
  methods: {
    loginType() {
      this.type = this.type === "" ? "" : "";
    },
    async submitForm() {
      let url = this.type !== "" ? login : register;
      let data = Object.assign({}, this.ruleForm);
      if (this.ruleForm.roleType == "3") {
        data.roleType = "2";
      }
      const result = await url(data);
      if (result.data.code == 1000) {
        this.$message.success(this.type === "" ? "" : "");
        // token
        if (this.type === "") {
          //token
          localStorage.setItem("TOKEN", result.data.data.token);
          this.$router.push("/");
          this.queryUserInfo();
        }
      } else {
        this.$message.error(result.data.msg);
      }
      console.log(result);
    },

    queryUserInfo() {
      getUserInfo().then((res) => {
        //
        localStorage.setItem("USERINFO", JSON.stringify(res.data.data));
      });
    },
  },
};
</script>
<style scoped lang="scss">
@import "@/css/login.scss";
.loginType {
  position: absolute;
  cursor: pointer;
  z-index: 1;
  button: 0px;
  right: 0px;
  color: white;
}
</style>
